/* ===== Base ===== */
* {
  box-sizing: border-box;
}

body {
  background: #eee;
  margin: 20px 0;
}

article {
  width: 21cm;
  min-height: 29.7cm;
  background: white;
  margin: 0 auto;
  overflow: auto;
  padding: 1em;
}

/* ===== Typography ===== */
p {
  margin: 0.3em 0;
}

h1 {
  font-size: 25px;
  margin-top: 0;
}

h2 {
  border-bottom: 1px solid #eee;
  padding-bottom: 8px;
  margin-bottom: 16px;
}

h3,
h4,
h5,
h6 {
  margin: 0;
}

h4 {
  display: inline;
  font-weight: bolder;
}

h3,
h5,
h6 {
  font-weight: normal;
}

ol,
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

strong {
  font-weight: normal;
  color: #f60;
}

a {
  color: #999;
  text-decoration: none;
  border-bottom: 1px solid;
}

p,
li {
  color: #666;
  line-height: 1.6;
}

/* ===== Components: Bio ===== */
.bio {
  position: relative;
}

.bio img {
  position: absolute;
  top: 0;
  right: 0;
}

/* ===== Components: Jobs ===== */
.jobs > ol > li {
  margin: 12px 0;
}

.jobs header {
  display: flex;
  color: #666;
  margin-bottom: 12px;
}

.jobs header > h3 {
  font-size: 18px;
  color: #333;
}

.jobs header > h4 {
  margin-left: 1em;
  font-size: 18px;
}

.jobs header > span {
  font-size: 18px;
  margin-left: auto;
}

/* ===== Components: Skills ===== */
.skills .wrapper {
  display: flex;
  justify-content: space-between;
}

.skills .wrapper > :first-child {
  margin-right: 24px;
}

.skills .wrapper ul {
  color: #333;
}

.skills .wrapper strong {
  color: #f60;
}

.skills .wrapper ul > li {
  line-height: 1.2;
  margin-bottom: 8px;
}

#skills {
  width: 500px;
  height: 270px;
}

/* ===== Components: Projects ===== */
.projects > ol {
  margin-top: 0;
  list-style-position: inside;
  padding: 0;
}

.projects > ol > li {
  margin-bottom: 24px;
}

.projects > ol > li > header {
  display: flex;
  justify-content: space-between;
}

.projects > ol > li h3 {
  display: inline-block;
}

/* ===== Components: Others ===== */
.others ul {
  list-style: disc;
  padding-left: 16px;
}

.education p {
  margin: 0.5em 0;
}

/* ===== Print ===== */
@media print {
  body {
    margin: 0;
  }

  .page2 {
    page-break-before: always;
    margin-top: 30px;
  }

  #skills {
    display: none;
  }
}

/* ===== Responsive ===== */
@media (max-width: 500px) {
  body {
    margin: 0;
  }

  article {
    width: auto;
    height: auto;
  }

  .bio img {
    display: none;
  }

  .skills .wrapper {
    flex-direction: column;
  }

  #skills {
    width: auto;
  }

  .jobs header {
    flex-direction: column;
  }

  .jobs header > h3,
  .jobs header > h4,
  .jobs header > span {
    margin: 0;
  }

  .imgSize {
    display: none;
  }
}
